.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding-top: 15%;
  text-align: center;
  box-sizing: border-box;
  background-image: url("../src/img/welcome.jpg"); /* Photo by Marvin Meyer from Unsplash */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-color: lightgray;
}
 .login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
  }

.privacy-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: white;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.privacy-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  }
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
  }
.google-btn:active {
    background: #1669f2;
    }
.google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
  }
  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
  }
.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
  background-color: #4285f4;
  width: 80%;
  height: 100%;
  text-align: center;
  margin-top: 0;
  margin-right: -5px;
}
h1 {
  color: #4285f4;
  font-size: 100px;
}

span {
  font-size: 20px;
  color: black;
  background-color: white;
}