@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: 'Archivo Narrow', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Oswald', sans-serif;
}

.App {
  text-align: center;
  padding: 2% 5%;
  font-family: 'Bree Serif', serif;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid black;
  background-color: #b3e0f2;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch {
  font-family: 'Bree Serif', serif;
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  font-family: 'Bree Serif', serif;
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

h2 {
  font-family: 'Bree Serif', serif;
  color: #415c67;
  font-size: 50px;
  padding-bottom: 20px;
  text-align: center;
}

.suggestions > li {
  font-family: 'Bree Serif', serif;
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #415c67;
  color: #b3e0f2;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #b3e0f2;
  color: #5e7e8b;
}

.EventList {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  color: #415c67;
}

@media screen and (max-width:767px) {
.EventList {
    grid-template-columns: repeat(1,1fr) !important;
}
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 3px solid black;
  border-radius: 4px;
  display: grid;
  position: relative;
  background-color: #b3e0f2;
}

.details-btn {
  font-family: 'Bree Serif', serif;
  background-color: #415c67;
  color: #8bb1c0;
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

label {
  color: #415c67;
}

.details-btn:hover {
  background-color: #8bb1c0;
  color: #415c67;
}

.scrollup-btn {
  font-family: 'Bree Serif', serif;
  position: fixed;
  bottom: 65px;
  right: 20px;
  border: none;
  border-radius: 5px;
  font-size: 1.125rem;
  background-color: #415c67;
  color: #8bb1c0;
  cursor: pointer;
  height: 35px;
  width: 35px;
}

.scrollup-btn:hover {
  background-color: #8bb1c0;
  color: #415c67;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

.Alert {
  position: fixed;
  top: 20px;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding-top: 15%;
  text-align: center;
  box-sizing: border-box;
  background-image: url(/meet-app/static/media/welcome.cf9de54f.jpg); /* Photo by Marvin Meyer from Unsplash */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-color: lightgray;
}
 .login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
  }

.privacy-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: white;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.privacy-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  }
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
  }
.google-btn:active {
    background: #1669f2;
    }
.google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
  }
  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
  }
.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
  background-color: #4285f4;
  width: 80%;
  height: 100%;
  text-align: center;
  margin-top: 0;
  margin-right: -5px;
}
h1 {
  color: #4285f4;
  font-size: 100px;
}

span {
  font-size: 20px;
  color: black;
  background-color: white;
}
